<template>
  <div class="full">
    <div class="flex-row">
      <div class="flex" style="flex: 1" :style="{height: totalheight}">
        <div class="flex col-sm-12" :style="{height: leftheight}">
          <installinfoselect v-ref:installselect></installinfoselect>
        </div>
        <div class="flex col-sm-12" v-if="show" style="padding-top: 3px;">
          <installinfodown v-ref:installdown></installinfodown>
        </div>
        <div class="col-sm-12" v-show="clishow">
          <div class="col-sm-3">
            <installfeeinfo :infeeinfo="selectdata" v-ref:installfeeinfo></installfeeinfo>
          </div>
          <div class="col-sm-9">
            <function-service-control v-ref:fsc :funcdata = "funcdata" :selectdata="selectdata"></function-service-control>
          </div>
        </div>
        <div v-show="comps" v-el:route class="flex span select-overspread"
             style="margin-top: 10px;border: 1px solid #EEF3F6;flex: 0.8;">
          <route v-ref:route></route>
        </div>
      </div>
      <div style="width: 350px;padding:0px 0px 10px 0px;margin-left: 20px">
        <install-apply-cardlist :btns="operatebtn" :userinfoid="row ? row.f_userinfo_id : ''" @list-select-oper="recordCancel"
                          v-ref:card></install-apply-cardlist>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import  {HttpResetClass} from 'vue-client'
  export default {
    title: '报建业务',
    data() {
      return {
        funcdata: null, // 选中功能
        comps: false,
        row: null,
        // arr: ['报建登记', '现场勘探', '设计出图'],
        selectdata: null,
        show: false,
        clishow: false,
        showallbtn: false,
        leftheight: '100%',
        totalheight: '100%',
        footheight: '0%'
      }
    },
   async created(){
      let http = new HttpResetClass()
     await  http.load('POST','rs/logic/ApplyGetConfigs', {data:this.$login.f}, {resolveMsg: null, rejectMsg: null}
      ).then((res)=>{
        if(res.data.code && res.data.code == 200){
          Vue.function_vue = Vue.prototype.$function_vue = res.data.function_vue
        }else{
          this.$showMessage('获取报建配置失败,原因:' + res.data.msg)
          return
        }
      }).catch((ret)=>{
        this.$showMessage('获取报建配置失败,原因:' + JSON.stringify(ret.data))
        return
      })
      console.log("Vue.$function_vue:"+JSON.stringify(this.$function_vue))
    },
    methods: {
      //    操作记录点击撤销时间调用方法
      recordCancel(title, name, row) {
        this.leftheight = '50%'
        this.showallbtn = false
        this.show = false
        this.clishow = false
        // this.$parent.$refs.installselect.bulkType = '操作记录撤销'
        this.$els.route.style.height = 260 + 'px'
        // 清除row的选择
        this.row = null
        // this.$refs.list.search()
        this.comps = {name: name, props: {data: row}}
        this.$refs.route.init(name, {data: row})
      }
    },
    events: {
      'func'(funcdata,row) {
        this.funcdata = funcdata
        this.selectdata = row
        this.leftheight = '25%'
        this.totalheight = '80%'
        this.$refs.installselect.buttonmsg = '返回'
        this.clishow = true
        this.$refs.fsc.hasChange(funcdata,row)
      },
      'showbtn'(val) {
        this.selectdata = val
        // this.show = true
        this.showallbtn = true
        this.leftheight = '25%'
        this.totalheight = '80%'
        this.$refs.installselect.buttonmsg = '返回'
      },
      'hiddenbtn'() {
        this.showallbtn = false
        this.clishow = false
        this.show = false
        this.comps = null
        this.leftheight = '100%'
        this.totalheight = '100%'
        this.$refs.installselect.buttonmsg = '查询'
        this.$refs.installselect.$refs.cp.$refs.cri.search()
        // this.$refs.installdown.$refs.cp.$refs.cri.search()
        this.$refs.card.$refs.paged.$refs.cri.search()
      },
      'showline'(value) {
        this.show = false
        this.$refs.installselect.clifun(value)
      },
      'searchcard'() {
        this.$refs.installselect.$refs.cp.$refs.cri.search()
        // this.$refs.installdown.$refs.cp.$refs.cri.search()
        this.$refs.card.$refs.paged.$refs.cri.search()
      },
      'list-select-oper'() {
        this.leftheight = '50%'
        this.footheight = '50%'
      }
    }
  }
</script>



